.menu {
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease, visibility .3s ease;

  list-style: none;
  height: calc(100vh - 89px);

  &.is-show{
    opacity: 1;
    visibility: visible;
    background-color: #373738;
    z-index: 2;

    display: flex;
    flex-direction: column;
    position: absolute;
    top: 89px;
    left: 0;
    margin: 0;
    min-width: 100%;
    .site-header__menu {
      flex-direction: column;
      margin-right: 0;
      padding-top: 30px;
      li {
        text-align: center;
        margin-bottom: 20px;
        margin-right: 0;
        a {
          font-size: 24px;
        }
        &.is-active {
          padding-top: 20px;
        }
      }
    }

    &:after {
      content: "";
      position: absolute;
      background-color: #373738;
      top: 0;
      left: -100vw;
      width: 200vw;
      height: 200vh;
      z-index: 2;
      overflow: hidden;
    }

    .menu {
      z-index: 100;
    }



  }

}
