.breadcrumbs {
  margin-top: 40px;

  ul {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include phones {
      justify-content: flex-start;
    }

    li {
      color: #828283;
      font-size: 11px;

      a {
        color: white;
        transition: color .3s ease;
        &:after {
          margin-left: 10px;
          margin-right: 10px;
          content: "/";
        }
        &:hover {
          color: $link-hover;
        }

      }

    }
  }
}
