// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------
.site-footer{
  padding: 45px 0 0;
  border-top: 12px solid #484747;

  &__title {
    font-size: 13px;
    text-transform: uppercase;
    color: #dddddd;
    margin-bottom: 25px;
  }
  nav {
    ul {

      li {

        a {
          font-size: 13px;
          line-height: 24px;
          color: #dddddd;
          transition: color .3s ease;
          &:hover {
            color: $link-hover;
          }
        }
      }
    }
  }

  .logo {
    margin-bottom: 30px;
  }

  .list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 5px 5px;
    li {

      a {
        img{
          display: block;
          width: 100%;
          transition: transform .3s ease;
        }
      }

      &:hover {
        a img {
          transform: scale(1.1);
        }
      }
    }
  }

  &__container {
    width: 24%;
    padding: 0;
    margin: 0;
    &:last-child {
      padding-left: 6px;
    }

    @include phones {
      width: 100%;
      padding-bottom: 20px;
      margin-bottom: 20px;
      border-bottom: 1px solid #8c8989;
      &:last-child {
        border-bottom: none;
        padding-bottom: 0;
      }
    }
    @include tablets {
      width: 40%
    }

    .more-info {

      p:first-child{
        margin-bottom: 30px;
      }
      p, a {
        font-size: 13px;
        transition: color .3s ease;
        color: #dddddd;
      }
      a:hover {
        color: #8c8989;
      }
    }

    .social {
      p, a {
        font-size: 13px;
      }
      p {
        color: #8c8989;
        &:first-child {
          padding-bottom: 22px;
          border-bottom: 1px solid #8c8989;
          margin-bottom: 25px;
        }
      }
      a {
        color: #dddddd;
        transition: color .3s ease;

        &:hover {
          color: #8c8989;
        }

      }
    }


  }

  &__layout {
    max-width: 940px;
    margin: 0 auto 10px;
    padding: 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include phones {
      flex-direction: column;
      padding: 0 15px;
    }
    @include tablets {
      flex-direction: row;
      flex-wrap: wrap;
      padding: 0 15px;
    }
  }

  &__copyright {
    background-color: #29292a;
    padding: 16px 0;
    border-top: 2px solid #4b4b4c;

    .layout {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include phones {
        flex-direction: column;
      }
    }
    p {
      font-size: 12px;
      line-height: 20px;
      color: #8c8989;

      a {
        color: #8c8989;
      }

      span {
        color: #bfbfbf;
      }

    }

  }
  .social-links {
    display: flex;
    justify-content: flex-end;

    li {
      margin-left: 20px;
      transition: scale .3s ease;
      &:hover {
        transform: scale(1.2);
      }
    }
  }

}
