.container {
  position: relative;
  width: 100%;
  border-bottom: 1px solid #585151;
  padding: 25px 0 40px;
  letter-spacing: 0.5px;

  .layout {
    max-width: 1000px;
  }
  &__title {
    font-size: 32px;
    line-height: 44px;
    text-align: center;
    margin-bottom: 32px;
    font-weight: normal;
  }
  p, li, a {
    font-size: 18px;
    line-height: 30px;
  }
  p {
    margin-bottom: 40px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  ul {

    li {
      list-style: none;
      position: relative;
      padding-left: 20px;

      &:before {
        content: "";
        width: 4px;
        height: 4px;
        background-color: $link-hover;
        border-radius: 50%;
        position: absolute;
        top: calc(50% - 2px);
        left: 0;
      }

    }
  }
  &__inner {
    width: 50%;

    &--text {
      padding: 0 15px;
      ul {
        margin-bottom: 30px;
      }
    }

    &--image {
      padding: 0 15px;

      @include phones{
        margin: 0 auto;
      }

      img {
        display: block;
        width: 100%;
      }
    }

    @include phones{
      width: 100%;
    }

  }
  &__full {
    width: 100%;
    ul {
      max-width: 680px;
      margin: 0 auto;
    }
  }
  &__half {
    width: 100%;
    justify-content: space-between;
    align-items: center;
    display: flex;

    @include phones{
      flex-direction: column;
    }
  }
}
.planning {
  padding: 30px 0 40px;
  margin-bottom: 12px;
  background-image: url("../../img/back-main.png");
  background-size: cover;
  letter-spacing: normal;

  .layout {
    max-width: 940px;
  }
}
