// -----------------------------------------------------------------------------
// This file contains all styles related to the button component.
// -----------------------------------------------------------------------------
.btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 20px;
  padding: 0 10px;
  border: none;
  text-align: center;
  background-color: $link-hover;
  border-radius: 10px;
  transition: transform .3s ease;

  span{
    width: 100%;
    text-align: center;
    color: white;
    font-size: 14px;
  }
}
.btn-submit {
  background-color: $link-hover;
  color: white;
  border: none;
  height: 32px;
  border-radius: 20px;
  max-width: 140px;
  margin: 0 0 0 auto;
  width: 100%;
  padding: 0 20px;
  transition: transform .3s ease;
}
