.contact-us {
  position: relative;
  background-image: url("../../img/back-contact.png");
  background-size: cover;
  background-repeat: no-repeat;
  padding: 104px 0 45px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include phones {
      flex-direction: column;
    }
  }

  &__form {

    form {
      width: 100%;
      max-width: 390px;
      display: flex;
      flex-direction: column;
      padding-left: 10px;

      @include desktop {
        padding-left: 0;
      }

      input {
        height: 35px;
        padding: 0 20px;
        margin-bottom: 26px;
        border: none;
      }
      input[type="email"] {
        margin-bottom: 20px;
      }
    }

  }

  &__title {

    p {
      font-size: 48px;
      line-height: 1;
      color: white;

      span {
        color: $link-hover;
      }

    }
  }
}
