.head{
  display: flex;
  align-items: center;
  height: 89px;
  background-image: url("../../pic/head-back.png");

  &__container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 940px;
    width: 100%;
    margin: 0 auto;
    padding: 0;

    @include tablets {
      padding: 0 15px;
    }

    h2 {
      font-size: 30px;
      color: white;
    }
  }
}
