.awards {
  padding: 38px 0 48px;
  background-color: #f6f6f6;

  &__title {
    font-size: 32px;
    line-height: 44px;
    text-align: center;
    margin-bottom: 50px;
    color: #333333;
    font-weight: normal;
  }
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;

    @include phones {
      flex-direction: column;
    }
    @include tablets {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
  &__item {
    margin: 0 40px;
    @include phones {
      margin: 40px 0;
    }
    @include tablets {
      margin: 0 20px;
    }
  }
}
