// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------
.site-header {
  background-color: #2F2F2F;
  height: 86px;
  width: 100%;
  display: flex;

  &__container {
    max-width: 940px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    @include desktop {
      padding: 0 15px;
    }

    @include tablets {
      .btn {
        display: none;
      }
    }

    @include tablets {
      padding: 0 15px;
    }
  }
  .menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 100%;

    @include tablets {
      flex-direction: column;
      display: none;
    }
    &.is-show {
      display: flex;
    }

  }
  &__menu {
    margin-right: 52px;
    height: 100%;
    align-items: center;
    position: relative;
    z-index: 3;
    display: flex;

    li {
      height: 100%;
      position: relative;
      display: flex;
      align-items: center;
      margin-right: 35px;

      &:last-child {
        margin-right: 0;
      }
        a:hover {
          color: $link-hover;
        }

      &.is-active {

        &:before {
          content: "";
          width: 100%;
          height: 2px;
          background-color: $link-hover;
          position: absolute;
          top: 0;
          left: 0;
        }

        a {
          color: $link-hover;
        }
      }

      a {
        font-size: 14px;
        color: white;
        transition: color .3s ease;
      }
    }
  }

}
